import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver, ModuleWithProvidersFactory } from '@nequi/features/commons';
import {
  MovementsInteractor,
  MovementsRepository,
  TicketRepository
} from '@nequi/features/movements/core';
import { ResourceId } from '@nequi/features/style-manager/core';

import { ContingencyPage } from '../backpack/contingency/contingency.page';
import { HomePage } from '../backpack/home/home.page';
import { TabLinkBackpackMiddleware } from './tab-link-backpack-middleware';
import { TabLinksPage } from './tab-links.page';
import { TabLinksConfig } from './tabs-links.config';

const routes: Routes = [
  {
    path: '',
    component: TabLinksPage,
    children: [
      {
        path: ResourceId.BACKPACK,
        component: HomePage,
        canActivate: [TabLinkBackpackMiddleware],
      },
      {
        path: ResourceId.BACKPACK_CONTINGENCY,
        component: ContingencyPage,
      },
      {
        path: ResourceId.CLOSET,
        loadChildren: () =>
          import('@nequi/features/closet/presentation').then((m) => m.FeaturesClosetModule),
      },
      {
        path: ResourceId.MOVEMENTS,
        loadChildren: () =>
          import('@nequi/features/movements/presentation').then(
            (m) =>
              new ModuleWithProvidersFactory(
                m.MovementsModule.withConfig([
                  {
                    provide: MovementsInteractor,
                    deps: [MovementsRepository, TicketRepository],
                    useClass: MovementsInteractor,
                  },
                ])
              )
          ),
      },
      { path: 'closet', redirectTo: ResourceId.CLOSET },
      { path: 'closet/', redirectTo: ResourceId.CLOSET },
      {
        path: '',
        redirectTo: ResourceId.BACKPACK,
        pathMatch: 'full',
      },
    ],
  },
  {
    path: '',
    redirectTo: ResourceId.BACKPACK,
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [
        TabLinksConfig.i18n.backpack,
        TabLinksConfig.i18n.profile,
        TabLinksConfig.i18n.moneyManagement,
        TabLinksConfig.i18n.contacts,
      ])
    ),
  ],
  exports: [RouterModule],
})
export class TabLinksRoutingModule {}
