import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { NavigatorProvider, SessionProvider } from '@nequi/features/commons';
import { ContingencyType } from '@nequi/features/commons/core';
import { ResourceId } from '@nequi/features/style-manager/core';

@Injectable()
export class TabLinkBackpackMiddleware implements CanActivate {
  constructor(
    private router: Router,
    private sessionProvider: SessionProvider,
    private navigatorProvider: NavigatorProvider
  ) {}

  public async canActivate(_route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    if (this.sessionProvider.contingencyType === ContingencyType.FINACLE) {
      this.navigatorProvider.navigateForward(`/admin/${ResourceId.BACKPACK_CONTINGENCY}`);
    }

    return true;
  }
}
