import { filter } from 'rxjs/operators';

import { Component, Injector, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { I18nConfig } from '@config/i18n.config';
import { TabLinksConfig } from './tabs-links.config';
import { IonTabBar, IonTabs } from '@ionic/angular';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  AppOptionsProvider,
  AuthProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import {
  UseMoneyStatus,
  UseMoneyStatusService
} from '@nequi/features/money-management/presentation';
import { ResourceId } from '@nequi/features/style-manager/core';
import { StyleManagerService } from '@nequi/features/style-manager/presentation';
import { AnalyticsInteractor } from '@nequi/features/commons/core';

import { TabItemOption } from './tab-item.option';
import { AnalyticsType } from '@nequi/features/commons/core/analytics';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { getFormattedDateTimes } from '@nequi/utils/date';

@Component({
  selector: 'app-tab-links',
  templateUrl: './tab-links.page.html',
  styleUrls: ['./tab-links.page.scss'],
})
export class TabLinksPage extends FeatureBasePage implements OnInit, OnDestroy {
  @ViewChild(IonTabBar) public tabBar: IonTabBar;
  @ViewChild(IonTabs) public tabs: IonTabs;

  public tabItems: TabItemOption[];
  public tabClass: string;
  public resourceId: ResourceId;
  public iconLoading: boolean[];
  private activeTab?: HTMLElement;

  constructor(
    private translateProvider: TranslateProvider,
    private styleManagerService: StyleManagerService,
    private alertService: AlertService,
    private authProvider: AuthProvider,
    injector: Injector,
    private useMoneyStatusService: UseMoneyStatusService,
    private appOptionsProvider: AppOptionsProvider,
    private router: Router,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    super(injector);
    this.translateProvider.loadModule(I18nConfig.modules.BACKPACK);

    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe((event: NavigationEnd) => {
        this.showHideTabs(event);
      });
  }

  public get selectedTabIndex() {
    if (this.tabItems.length === 0 || !this.tabBar || !this.tabBar?.selectedTab) {
      return;
    }

    return this.tabItems.findIndex((tab) => tab.id === this.tabBar.selectedTab);
  }

  public async ngOnInit() {
    this.resourceId = ResourceId.BAR;
    this.useMoneyStatusService.subscribe((status) => this.updateTabsClass(status));
    this.tabItems = this.appOptionsProvider.getTabs();

    this.iconLoading = new Array(this.tabItems.length);
    this.loadIcons();
  }

  public ngOnDestroy() {
    this.useMoneyStatusService.unsubscribe();
  }

  public goBack() {
    this.alertService.show({
      messages: {
        header: '¿Estas seguro que quieres salir de Nequi?',
        message: 'Es mejor que cierres tu sesión cuando no estés usando Nequi',
        buttons: {
          accept: 'Acepta',
          cancel: 'Cancela',
        },
      },
      handlers: {
        accept: () => this.authProvider.signOut(),
      },
    });
  }

  public tabChange(tabsRef: IonTabs) {
    this.activeTab = tabsRef.outlet.activatedView.element;

    if(this.tabBar.selectedTab === ResourceId.CLOSET){
      this.setAnalyticsIngressIntention();
    }
  }

  public ionViewWillLeave() {
    super.ionViewWillLeave();
    this.propagateToActiveTab('ionViewWillLeave');
  }

  public ionViewDidLeave() {
    this.propagateToActiveTab('ionViewDidLeave');
  }

  public ionViewWillEnter() {
    this.propagateToActiveTab('ionViewWillEnter');
  }

  public ionViewDidEnter() {
    super.ionViewDidEnter();
    this.propagateToActiveTab('ionViewDidEnter');
  }

  public updateLoadingStatus(tabIndex: number, value: boolean) {
    this.iconLoading[tabIndex] = value;
  }

  private async setAnalyticsIngressIntention() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: TabLinksConfig.analytics.events.intentionOfIngress.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: TabLinksConfig.analytics.events.intentionOfIngress.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: TabLinksConfig.analytics.events.intentionOfIngress.name,
        eventProps: stringsArrayToObjectWithValues(
          TabLinksConfig.analytics.events.intentionOfIngress.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }

  private propagateToActiveTab(eventName: string) {
    if (this.activeTab) {
      this.activeTab.dispatchEvent(new CustomEvent(eventName));
    }
  }

  private async loadIcons() {
    const DEFAULT_ICON = 'dashboard';
    const icons = await this.styleManagerService.getIcons(this.resourceId);

    this.tabItems.forEach((tabItem) => {
      tabItem.setIcon(icons[tabItem.id] ?? icons[DEFAULT_ICON]);
    });
  }

  private updateTabsClass(status: UseMoneyStatus) {
    this.tabClass = status === UseMoneyStatus.OPENED ? 'hidden' : '';
  }

  private showHideTabs(event: NavigationEnd) {
    const urlSplitted = event?.url?.split('/').filter((segment) => segment.length > 0);
    const fullPageUrl = urlSplitted[urlSplitted.length - 1];
    const pageUrl = fullPageUrl?.split('?')[0];
    const CLOSET = 'closet';

    const showTabBarPages = [
      ResourceId.ADMIN,
      ResourceId.BACKPACK,
      ResourceId.BACKPACK_CONTINGENCY,
      ResourceId.CLOSET,
      ResourceId.MOVEMENTS,
      CLOSET,
    ];

    this.tabClass = showTabBarPages.indexOf(pageUrl as ResourceId) >= 0 ? '' : 'hidden';
  }
}
